<template>
    <div class="box box-primary">
        <div class="box-header with-border">
            <h3 class="box-title">Quản lý sản phẩm</h3>
            <div class="row product-controller">
                <div class="col-md-6 col-xs-6">
                    <Select2 v-model="id_category" 
                    :options="categories(false)" 
                    @change="changeCategory($event)" />
                </div>
                <div class="col-md-4 col-xs-3">
                    <Select2 v-model="productStatus" 
                    :options="dataStatus" 
                    @change="changeStatusProduct($event)" />
                </div>
                <div class="col-md-2 col-xs-3">
                    <Select2 v-model="pageSize" 
                    :options="[{id:10, text: '10sp/Trang'}, {id:20, text: '20sp/Trang'}, {id:50, text: '50sp/Trang'}]" 
                    @change="changePageSize($event)" />
                </div>
            </div>
            <div class="input-group user-product-search">
                <input v-on:keyup="search" type="text" class="form-control" placeholder="Tìm kiếm... ">
                <span class="input-group-addon" title="Tìm"><i class="fa fa-search"></i></span>
            </div>
        </div>
        <div class="box-body no-padding" id="_user-product">
            <div v-for="(item, index) in product" :key="index" class="item">
                <div class="img">
                    <img  :src="api + '/image/product/thumbnail/'+item.image" :alt="item.name">
                    <span class="status">
                        {{ dataStatus.find(e=>e.id == item.status).text }}
                    </span>
                </div>
                <div class="about">
                    <h2 class="name">
                        <router-link :to="{name:'route.product', params:{province: item.url_province, district: item.url_district, category: item.url_category, id: item.id, product: item.url }}">Xem chi tiết</router-link> {{ item.name }}</h2>
                    <div class="bottom">
                        <h3 class="category">{{ item.category }}</h3>
                        <h3 v-if="item.price!=0" class="price">{{ number_format(item.price,0) }} đ</h3>
                        <h3 v-if="item.price==0" class="price">Thoả thuận</h3>
                    </div>
                </div>
                <div class="controller">
                    <router-link v-if="item.status!=2" :to="{name: 'user.editproduct', params:{id: item.id}}" class="btn btn-primary">Sửa</router-link>
                    <a v-if="item.status==1" v-on:click="soldProduct(index)" class="btn btn-success">Đã bán</a>
                    <a v-if="item.status==2" v-on:click="activeProduct(index)" class="btn btn-success">Bán lại</a>
                    <a v-on:click="deleteProduct(index)" class="btn btn-danger">Xoá</a>
                </div>
            </div>
            <paginate v-model="page"
                :page-count="pageCount"
                :page-range="3"
                :margin-pages="2"                                           
                :click-handler="runPage"
                :prev-text="`<i class='fa fa-angle-left' aria-hidden='true'></i>`"
                :next-text="`<i class='fa fa-angle-right' aria-hidden='true'></i>`"
                :container-class="'pagination'"
                :page-class="'page-item'">
            </paginate>
        </div>
    </div>
</template>

<script>
import moment from "moment";
import {mapGetters} from "vuex";
import axios from "axios";
import Select2 from "v-select2-component";
import paginate from "vuejs-paginate";

export default {
    metaInfo(){
		return {
			title: "Quản lý tin",
			htmlAttrs: {
				lang: 'vi',
				amp: true
			}
		}
	},
    components:{Select2, paginate},
    data(){
        return {
            api: this.$store.state.domainApi,
            id_category: 'tat-ca-danh-muc',
            productStatus: this.$route.query.status ? this.$route.query.status : 'tat-ca-trang-thai',
            dataStatus: [
                {id:'tat-ca-trang-thai', text: 'Tất cả trạng thái'},
                {id:0, text: 'Chưa duyệt'},
                {id:1, text: 'Đã duyệt'},
                {id:2, text: 'Đã bán'},
                {id:3, text: 'Vi phạm'}
            ],
            page:1,
            pageSize:20,
            pageCount:0,
            product:[]
        }
    },
    methods:{
        getTime(date){
            var time = new moment(date);
            return time;
        },
        number_format( number, decimals, dec_point, thousands_sep ) {
            var n = number, c = isNaN(decimals = Math.abs(decimals)) ? 2 : decimals;
            var d = dec_point == undefined ? "," : dec_point;
            var t = thousands_sep == undefined ? "." : thousands_sep, s = n < 0 ? "-" : "";
            var i = parseInt(n = Math.abs(+n || 0).toFixed(c)) + "", j = (j = i.length) > 3 ? j % 3 : 0;
                                    
            return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "");
        },
        changeCategory(){
            this.page = 1;
            this.getData();
        },
        changePageSize(size){
            this.pageSize = size;
            this.page = 1;
            this.getData();
        },
        changeStatusProduct(){
            this.page = 1;
            this.getData();
        },
        runPage(page){
            this.page = page;
            this.getData();
        },
        search(e){
            this.page = 1;
            this.getData(e.target.value); 
        },
        getData(search=''){
            axios.get(this.api + "/auth/searchproduct", {
                params:{
                    id_user: this.$store.state.user.id,
                    id_category: this.id_category,
                    status: this.productStatus,
                    search: search,
                    page: this.page,
                    pageSize: this.pageSize,
                }
            })
            .then(response=>{
                this.product = response.data.product;
                this.pageCount = Math.ceil(response.data.count/this.pageSize);
            })
            .catch(error=>{
                console.log(error);
            });
        },
        soldProduct(index){
            this.$swal.fire({
                title: 'Xác nhận sản phẩm đã bán !',
                text: "",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Không!',
                confirmButtonText: 'Đã bán!'
            }).then((result) => {
                if (result.value) {
                    axios.put(this.api + '/auth/soldproduct',{
                        id: this.product[index].id
                    },{
                        headers:{
                            Authorization: `Bearer ${this.$store.state.token}`
                        }
                    })
                    .then(response=>{
                        if(response.data.success){
                            this.flash(response.data.message, 'success', { timeout: 3000 });
                            this.product.splice(index,1);
                        }
                        else this.flash(response.data.message, 'error', { timeout: 3000 });
                    })
                }
            }); 
        },
        activeProduct(index){
            this.$swal.fire({
                title: 'Bạn muốn đăng lại sản phẩm này !',
                text: "",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Không!',
                confirmButtonText: 'Đăng lại!'
            }).then((result) => {
                if (result.value) {
                    axios.put(this.api + '/auth/activeproduct',{
                        id: this.product[index].id
                    },{
                        headers:{
                            Authorization: `Bearer ${this.$store.state.token}`
                        }
                    })
                    .then(response=>{
                        if(response.data.success){
                            this.flash(response.data.message, 'success', { timeout: 3000 });
                            //this.product.splice(index,1);
                        }
                        else this.flash(response.data.message, 'error', { timeout: 3000 });
                    })
                }
            }); 
        },
        deleteProduct(index){
            this.$swal.fire({
                title: 'Bán có muốn xoá !',
                text: "Xoá vĩnh viễn sản phẩm này",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Không !',
                confirmButtonText: 'Xoá !'
            }).then((result) => {
                if (result.value) {
                    axios.delete(this.api + '/auth/deleteproduct',{
                        headers:{
                            Authorization: `Bearer ${this.$store.state.token}`
                        },
                        params:{
                            id: this.product[index].id,
                            id_group_image: this.product[index].image
                        }
                    })
                    .then(response=>{
                        if(response.data.success){
                            this.flash(response.data.message, 'success', { timeout: 3000 });
                            this.product.splice(index,1);
                        }
                        else this.flash(response.data.message, 'error', { timeout: 3000 });
                    })
                }
            }); 
        }
    },
    computed:{
        ...mapGetters({user: 'getUser', categories: 'getCategorySort'})
    },
    created(){
        moment.locale("vi");
        this.getData();
    }
}
</script>

